// *, *::before, *::after
//     box-sizing: border-box

body {
    line-height: 1.5; }

img {
    display: block; }

a {
    text-decoration: none;
    display: block;
    color: inherit;
    &:hover {
        text-decoration: none; } }

h1 {
    font-size: 0; }

h1,h2,h3,h4,h5,h6,p {
    margin-bottom: 0; }

small {
    font-size: .75rem; }
